export const assertPromiseDoesntError = (promise, done) => {
    promise
        .then((result) => {
        done();
    })
        .catch((error) => {
        done(error);
    });
};
export const assertPromiseDoesError = (promise, done) => {
    promise
        .then((result) => {
        done("Promise should have errored");
    })
        .catch((error) => {
        done();
    });
};
export const assertPromiseErrorsWithCode = (promise, errorCode, done) => {
    promise
        .then((result) => {
        done("Promise should have errored");
    })
        .catch((error) => {
        if (error.errorCode == errorCode) {
            done();
        }
        else {
            done(`Promise should have errored with errorCode ${errorCode} but instead
        errored with errorCode ${error.errorCode}`);
        }
    });
};
